.doc-container {
    flex:1;
    width:100%;
    max-width:60em;
    padding-left:16em;
}

@media (max-width:45em) {
  .doc-container {
      padding-left:0;
  }
}

.doc-content {
    display:block;
    width:100%;
    text-align:left;
    position: relative;
}

.doc-content > * {
    margin-bottom: 1.25rem;
}
.doc-content > h1,
.doc-content > h2,
.doc-content > h3,
.doc-content > h4,
.doc-content > h5,
.doc-content > h6 {
    padding-top:1.25rem;
}
.doc-content > h1:first-child {
    padding-top:0;
}

.doc-content > h1,
.doc-content > h2 {
  border-bottom:1px solid #f2f4f6;
}

.doc-layout {
    display: flex;
    flex-direction:row;
    width:100%;
    padding:2em;
}


.doc-content .doc-footer {
    background: transparent;
    margin-bottom:-2em;
}

.markdown-table {
  border-collapse: collapse;
  font-size: 0.85em;
}
.markdown-table th,
.markdown-table td {
  padding: 0.5em 1em;
  border: 1px solid #dbdddf;
}
.markdown-table th {
  background: #09c;
  border-color: #09c;
  color: #fff;
  font-weight: 600;
}

blockquote.protip {
  border-left-color: #ffc800;
  background: #fff8f0;
}
